




















































































































































































// 
import eventRegisterOptions from './listDiaConfig/eventRegisterOptions.vue';
// 
import handleOptions from './listDiaConfig/handleOptions.vue';
//<!--  -->
import formControl from './customForm/edit/index.vue';
// 
import NumberInput from '@components/NumberInput/index.vue';
// 
import decisionRuleEditor from './lanchConfig/decisionRuleEditor.vue';
// 
import operatingConfig from './listDiaConfig/operatingConfig.vue';
import Base64 from '@utils/Base64';
import {
  addTaskRela,
  updateTaskRela,
  detailsTaskRela
} from '@api/taskLibraryManage';
import {
  queryLinkInfoDropout,
  getOperationParams
} from '@api/businessScenarioManage';
import { Vue, Component, Prop, Emit, Ref, Watch } from 'vue-property-decorator';
import cloneDeep from 'lodash/cloneDeep';
import { namespace } from 'vuex-class';
const taskLibraryManage = namespace('taskLibraryManage');
import { dataViewFields } from '@api/viewData';

@Component({
  components: {
    NumberInput,
    eventRegisterOptions,
    handleOptions,
    decisionRuleEditor,
    formControl,
    operatingConfig
  }
})
export default class HandleListDia extends Vue {
  name: 'HandleListDia';
  @taskLibraryManage.State('taskLib') taskLib;
  @taskLibraryManage.State('customForm') customForm;
  @taskLibraryManage.State('formItem') formItem;
  @taskLibraryManage.Mutation('INITEVENTREGISTPOOL') initeventregistpool;
  @taskLibraryManage.Mutation('UPDATEDEALINFO') updateDealInfo;

  // 
  @taskLibraryManage.Mutation('CHANGEFORMFLAG') changeFormFlag: any;

  get init() {
    console.log(this.$store.state.taskLibraryManage.formFlag);
    return this.$store.state.taskLibraryManage.formFlag;
  }
  @Ref() handleOptions;
  @Ref() treeRef;
  @Prop({ default: false }) isCopy: boolean;
  @Prop({ default: false }) visible: string;
  @Watch('visible')
  visibleChange(val) {
    if (val) {
      this.tabActive = 'basicInfo';
      this.loadData();
      this.$store.dispatch('taskLibraryManage/getallFieldsData');
      this.getViewDataOptions(); // 
    }
  }
  @Prop() data: Record<string, any>;

  @Ref() basicInfoForm;
  @Ref() saveTagInput;
  @Emit('cancelVisible')
  cancel() {}

  private basicInfo: Record<string, any> = {
    id: '',
    dealRequest: {},
    paramRequest: [], //
    relaName: '', // 
    relaType: 0, // 
    relaId: '', // xx 
    startRule: [], // 
    execMenthod: 1, // 
    requenceNum: '', // 
    requenceType: '', // 
    startDate: '', // 
    endDate: '', // 
    allowHand: false // 
  };
  private loading = false;
  private dataViewList: Array<any> = [];
  private rules = {
    relaName: [{ required: true, trigger: 'blur', message: '' }],
    relaType: [{ required: true, trigger: 'change', message: '' }]
  };
  // 
  private requenceTypeOptions = [
    {
      value: 0,
      label: ''
    },
    {
      value: 1,
      label: ''
    },
    {
      value: 2,
      label: ''
    },
    {
      value: 3,
      label: ''
    },
    {
      value: 4,
      label: ''
    },
    {
      value: 5,
      label: ''
    },
    {
      value: 6,
      label: ''
    },
    {
      value: 7,
      label: ''
    }
  ];
  // 
  private basicOptions = [
    {
      value: 0,
      label: ''
    },
    {
      value: 1,
      label: ''
    },
    {
      value: 2,
      label: ''
    },
    {
      value: 3,
      label: 'RPA'
    },
    {
      value: 4,
      label: 'OCR'
    }
    // {
    //   value: 5,
    //   label: ''
    // }
  ];
  private options = [];

  private trans = {
    2: '',
    3: 'RPA',
    4: 'OCR'
  };
  private value = '';
  private inputVisible = false;
  private inputValue = '';

  private tabActive = 'basicInfo';

  private registtabActive = 'startUpEvent';
  private tabList: Array<any> = [
    { name: '', id: '0', component: 'startUpEvent' },
    { name: '', id: '1', component: 'readyEvent' },
    { name: '', id: '2', component: 'startEvent' },
    { name: '', id: '3', component: 'doingEvent' },
    { name: '', id: '4', component: 'successEvent' },
    { name: '', id: '5', component: 'failEvent' },
    { name: '', id: '6', component: 'endEvent' },
    { name: '', id: '7', component: 'operationEndEvent' }
  ];
  get settingShow() {
    const { relaType } = this.basicInfo;
    return relaType != 5;
  }
  loadData() {
    const { id } = this.data;
    this.changeFormFlag(false);
    if (id) {
      this.loading = true;
      detailsTaskRela({ id }).then(({ data, error }) => {
        if (!error) {
          this.loading = false;
          data.startRule = JSON.parse(data.startRule);
          this.basicInfo = cloneDeep(data);
          if (this.basicInfo.relaId) {
            this.changeOperationType('true'); // 
            this.getHandleSetting();
          }
          this.initeventregistpool(data.event); // 
          //   
          this.updateDealInfo({
            relaId: data.relaId,
            relaName: data.relaName
          }); // 
          // 
          if (data.form) {
            this.$store.dispatch(
              'taskLibraryManage/getCustomForm',
              JSON.parse(Base64.decode(data.form))
            );
            this.$store.dispatch('taskLibraryManage/getFormItem', data.form);
          }
        } else {
          this.loading = false;
          this.$message.error(`${error}`);
        }
      });
    } else {
      this.basicInfo = {
        id: '',
        dealRequest: {},
        relaName: '', // 
        relaType: 0, // 
        relaId: '', // xx 
        startRule: [], // 
        execMenthod: 1, // 
        requenceNum: '', // 
        requenceType: '', // 
        startDate: '', // 
        endDate: '', // 
        allowHand: false // 
      };
      this.initeventregistpool(); // 
      this.updateDealInfo({
        relaId: '',
        relaName: ''
      });

      // 
      this.$store.dispatch('taskLibraryManage/getFormItem', '');
      this.$store.dispatch('taskLibraryManage/getCustomForm', { formList: [] });
    }
  }
  jungleShow() {
    return this.basicInfo.relaType > 1 && this.basicInfo.relaType < 5;
  }
  resetData() {
    // vuex 
    this.basicInfoForm.resetFields();
    this.cancel();
  }

  // 
  getViewDataOptions() {
    const param: any = {};
    param.type = '0';

    dataViewFields(param).then(({ data, error }) => {
      if (!error) {
        this.dataViewList = data;
      } else {
        this.$message.error(error.message);
      }
    });
  }

  //
  confirm() {
    if (this.basicInfo.startDate > this.basicInfo.endDate) {
      this.$message.error('');
      return false;
    }

    // 
    const validForm = this.handleOptions.validForm();
    console.log('valid', validForm);
    if (validForm) {
      // 
      let dealRequest = {};
      let paramRequest = [];
      if (this.basicInfo.relaType > 1 && this.basicInfo.relaType < 5) {
        // table
        paramRequest = this.handleOptions.getSetting();
      } else if (this.basicInfo.relaType < 2) {
        // form
        dealRequest = this.handleOptions.getDealRequest();
      }
      // 
      const startRule = JSON.stringify(this.treeRef.getCurrentRule());
      const data = {
        ...this.basicInfo,
        dealRequest,
        paramRequest,
        startRule,
        form: this.formItem
      };
      if (!this.taskLib.id) {
        this.$message.error('');
      } else {
        this.basicInfoForm.validate((valid) => {
          if (valid) {
            if (this.isCopy) {
              delete this.basicInfo.id;
            }
            if (!this.basicInfo.id) {
              addTaskRela({ ...data, taskId: this.taskLib.id }).then((res) => {
                if (!res.error) {
                  this.resetData();
                } else {
                  this.$message.error(`:${res.error.message}`);
                }
              });
            } else {
              updateTaskRela({ ...data, taskId: this.taskLib.id }).then(
                (res) => {
                  if (!res.error) {
                    this.resetData();
                  } else {
                    this.$message.error(`:${res.error.message}`);
                  }
                }
              );
            }
          } else {
            return false;
          }
        });
      }
    }
  }
  // 
  changeOperationType(init = '') {
    if (Object.prototype.toString.call(init) === '[object Number]') {
      this.$set(this.basicInfo, 'relaId', '');
    }
    const val = this.basicInfo.relaType;
    if (val > 1 && val < 5) {
      queryLinkInfoDropout({ type: val }).then((res) => {
        if (!res.error) {
          this.options = res.data;
        }
      });
    }
  }
  // 
  getHandleSetting() {
    const data = { id: this.basicInfo.relaId, type: this.basicInfo.relaType };
    getOperationParams(data).then((res) => {
      // 
      this.basicInfo.paramRequest = res.data;
    });
  }

  // 
  registTabChoose(currentCom: string) {
    this.registtabActive = currentCom;
  }
}
