




















































































































































































































































































































































import NumberInput from '@components/NumberInput/index.vue';
import {
  listTag,
  getAllGroupAndUserDetail,
  getUserInfoByStationId
} from '@api/taskLibraryManage';
import { getStageTaskByScenesId } from '@api/businessScenarioManage';
const checkTags = (rule, value, callback) => {
  console.log(value);
  if (!value.length) {
    callback(new Error(''));
  } else {
    callback();
  }
};
import { Vue, Component, Ref, Prop, Watch, Emit } from 'vue-property-decorator';
import { namespace } from 'vuex-class';
import cloneDeep from 'lodash/cloneDeep';

const taskLibraryManage = namespace('taskLibraryManage');
@Component({
  components: { NumberInput }
})
export default class BasicInfo extends Vue {
  name: 'BasicInfo';

  @taskLibraryManage.Mutation('UPDATE_BASICINFO') updateBasicinfo;
  @taskLibraryManage.Mutation('UPDATETIMEOUTREMIND') updateTimeOutRemind;
  @taskLibraryManage.Mutation('UPDATETAGS') updateTags;

  @Ref() basicInfoForm;
  @Ref() reminderConForm;
  @Ref() saveTagInput;
  @Prop() data;
  @Watch('data', { deep: true, immediate: true })
  dataChange(val) {
    console.log('basicInfo', val);
    const { scenesId, scenesType } = this.$route.query;
    if (val && val.taskLib && JSON.stringify(val.taskLib) !== '{}') {
      this.basicInfo = cloneDeep(val.taskLib);
      this.tags = cloneDeep(val.tags);

      // null
      const timeOutRemind: any = {
        reminderType: 1, //	
        phoneNumber: '', // 
        wxNumber: '', // 
        addressee: '', // 
        ccPerson: '', // 
        id: '',
        reminderContent: '', //
        reminderFrequency: 0, //
        reminderTitle: '', //
        endOffsetDays: 0, //
        endOffsetUnit: 0, //	
        endTime: '', //	
        reminderUnit: 1, //
        startOffsetDays: 0, //	
        startOffsetUnit: '', //	
        startTime: '', //	
        taskId: ''
      };

      this.timeOutRemind = val.timeOutRemind
        ? cloneDeep(val.timeOutRemind)
        : timeOutRemind;
      console.log('this.timeOutRemind', this.timeOutRemind);
    }
    this.basicInfo.scenesId = scenesId;
    this.basicInfo.scenesType = scenesType || '';
    console.log('basicInfo', this.basicInfo);
    if (this.basicInfo.taskStation) {
      this.getStationUser();
    }
  }
  @Watch('basicInfo', { deep: true, immediate: true })
  // vuex
  basicInfoChange(val) {
    // this.$store.commit('taskLibraryManage/UPDATE_BASICINFO', val);
    this.updateBasicinfo(val);
  }
  @Watch('timeOutRemind', { deep: true, immediate: true })
  // vuex
  timeOutRemindChange(val) {
    this.updateTimeOutRemind(val);
  }
  @Watch('tags', { deep: true, immediate: true })
  tagsChange(val) {
    console.log(val);
    this.updateTags(val);
  }

  get stationUserDisable() {
    return this.basicInfo.taskStation !== '';
  }
  private basicInfo: any = {
    taskName: '', // 
    dealStrategy: '', // 
    taskDesc: '', // 
    scenesId: '', // 
    scenesType: '', // 
    stageId: '', // 
    taskStation: '', // 
    taskStationName: '', // 
    stationUser: '', // 
    // timeout: new Date(2016, 9, 10, 18, 40), // 
    timeoutTime: 0, // 
    timeoutUnit: 1, // 
    isRelatedProducts: 0, // 
    isMonthlyTarget: 0 // 
  };
  private timeOutRemind: any = {
    reminderType: 1, //	
    phoneNumber: '', // 
    wxNumber: '', // 
    addressee: '', // 
    ccPerson: '', // 
    id: '',
    reminderContent: '', //
    reminderFrequency: 0, //
    reminderTitle: '', //
    endOffsetDays: 0, //
    endOffsetUnit: 0, //	
    endTime: '', //	
    reminderUnit: 1, //
    startOffsetDays: 0, //	
    startOffsetUnit: '', //	
    startTime: '', //	
    taskId: ''
  };
  private tags: Array<any> = []; // 
  private rules = {
    taskName: [{ required: true, trigger: 'blur', message: '' }],
    dealStrategy: [
      { required: true, trigger: 'change', message: '' }
    ],
    // taskStation: [
    //   { required: true, trigger: 'blur', message: '' }
    // ],
    tags: [{ required: true, validator: checkTags, trigger: 'change' }]
  };
  private remindRules = {
    phoneNumber: [
      { required: true, message: '', trigger: 'blur' },
      {
        pattern: /^((0\d{2,3}-\d{7,8})|(1[3584]\d{9}))$/,
        message: '/',
        trigger: 'blur'
      }
    ],
    wxNumber: [{ required: true, trigger: 'blur', message: '' }],
    addressee: [{ required: true, trigger: 'blur', message: '' }]
  };

  private inputVisible = false;
  private inputValue = '';

  private taskStationLoading = false;
  private stationUserLoading = false;
  private tagVisible = false; // 
  private tagValue = ''; // 
  private tagsList: Array<any> = []; // 
  private taskStationOptions: Array<any> = []; //
  private stationUserOptions: Array<any> = []; //
  private stageOptions: Array<any> = []; //

  private dealStrategyOptions: Array<any> = [
    { value: 0, label: '' },
    { value: 1, label: '' },
    { value: 2, label: '' }
  ];
  private endOffsetUnitOptions: Array<any> = [
    { value: 0, label: '' },
    { value: 1, label: '' }
  ];

  private timeoutUnitOptions: Array<any> = [
    { value: 1, label: '' },
    { value: 2, label: '' },
    { value: 3, label: '' },
    { value: 4, label: '' },
    { value: 5, label: '' }
  ];
  private reminderTypeOptions = [
    { value: 1, label: '' },
    { value: 2, label: '' },
    { value: 3, label: '' },
    { value: 4, label: '' }
  ];
  private reminderUnitOptions = [
    { value: 1, label: '' },
    { value: 2, label: '' },
    { value: 3, label: '' },
    { value: 4, label: '' },
    { value: 5, label: '' },
    { value: 6, label: '' },
    { value: 7, label: '' }
  ];
  private timeoutStrategyOptions = [
    { value: 1, label: '' },
    { value: 2, label: '' }
  ];

  loadTags() {
    listTag().then((res) => {
      this.tagsList = res.data;
    });
  }
  // 
  loadGroup() {
    this.taskStationLoading = true;
    getAllGroupAndUserDetail().then((res) => {
      if (!res.error) {
        this.taskStationOptions = res.data;
      } else {
        this.$message.error(`:${res.error.message}`);
      }
      this.taskStationLoading = false;
    });
  }
  loadScenes() {
    const { scenesId } = this.$route.query;
    getStageTaskByScenesId({ scenesId }).then((res) => {
      if (!res.error) {
        this.stageOptions = res.data.stageTasksResponses || [];
      } else {
        this.$message.error(`:${res.error.message}`);
      }
    });
  }

  // 
  showTags() {
    this.tagVisible = true;
  }

  // 
  handleInputConfirm() {
    const val = this.tagValue;
    if (val.trim()) {
      if (this.tagsList.indexOf(val) === -1) {
        this.tagsList.push(val);
      }
      if (this.tags.indexOf(val) === -1) {
        this.tags.push(val);
      }
    }
  }

  // 
  addTags(val) {
    if (this.tags.indexOf(val) === -1) {
      this.tags.push(val);
    }
  }
  // 
  getStationUser(val = '') {
    this.$set(this.basicInfo, 'stationUser', '');
    this.taskStationOptions.forEach((item) => {
      if (val === item.id) {
        this.basicInfo.taskStationName = item.station;
      }
    });
    const data = {
      stationId: this.basicInfo.taskStation,
      status: '0'
    };

    this.stationUserLoading = true;
    getUserInfoByStationId(data).then((res) => {
      if (!res.error) {
        this.stationUserOptions = res.data;
      } else {
        this.$message.error(`:${res.error.message}`);
      }
      this.stationUserLoading = false;
    });
  }
  // 
  changeStationUser(val) {
    this.stationUserOptions.forEach((item) => {
      if (val === item.id) {
        this.basicInfo.stationUserName = item.user;
      }
    });
  }

  validData() {
    let flag = true;
    const { startTime, endTime } = this.timeOutRemind;
    if (startTime > endTime) {
      this.$message.error('');
      flag = false;
    }
    this.reminderConForm.validate((valid) => {
      if (!valid) {
        flag = false;
      }
    });
    this.basicInfoForm.validate((valid) => {
      if (!valid) {
        flag = false;
      }
    });
    return flag;
  }

  handleClose(tag) {
    this.tags.splice(this.tags.indexOf(tag), 1);
  }

  created() {
    this.loadTags();
    this.loadGroup();
    this.$route.query.scenesId && this.loadScenes();
  }
}
