




























































































































import { Component, Vue, Prop, Emit, Ref, Watch } from 'vue-property-decorator';
import decisionRuleEditor from '../lanchConfig/decisionRuleEditor.vue';
import scriptEdit from '../operationSet/scriptEdit.vue';
import {
  reminderSaveEvent,
  reminderUpdateEvent,
  reminderViewEvent
} from '@api/taskLibraryManage';
import NumberInput from '@components/NumberInput/index.vue';
import { namespace } from 'vuex-class';
const taskLibraryManage = namespace('taskLibraryManage');

@Component({
  components: { decisionRuleEditor, NumberInput, scriptEdit }
})
export default class MailDialog extends Vue {
  @Prop({ default: false }) dialogVisible!: boolean;
  @Emit('cancel') cancel() {}
  @Prop() registtabActive;
  @Ref() treeRef: any;
  @Ref() ruleFormRef: any;
  @Prop() addIndex;

  @taskLibraryManage.Mutation('UPDATEEVENTREGISTPOOL') updateEventRegistpool;

  private trans = {
    startUpEvent: 0,
    readyEvent: 1,
    startEvent: 2,
    doingEvent: 3,
    successEvent: 4,
    failEvent: 5,
    endEvent: 6,
    operationEndEvent: 7
  };
  private ruleForm: any = {
    id: null,
    registerName: '',
    reminderType: 1, // 
    noticeTile: '', // 
    noticeContent: '', // 
    requenceNum: 0, // 
    startTime: '',
    endTime: '',
    rule: [],
    cc: '',
    type: 1,
    weChatUser: '',
    recipient: '',
    phoneNumber: ''
  };

  rulesMail: any = {
    registerName: [
      { required: true, message: '', trigger: 'blur' }
    ],
    phoneNumber: [
      { required: true, message: '', trigger: 'blur' },
      {
        pattern: /^((0\d{2,3}-\d{7,8})|(1[3584]\d{9}))$/,
        message: '/',
        trigger: 'blur'
      }
    ],
    weChatUser: [{ required: true, trigger: 'blur', message: '' }],
    recipient: [{ required: true, trigger: 'blur', message: '' }]
  };
  private reminderTypeOptions = [
    { value: 1, label: '' },
    { value: 2, label: '' },
    { value: 3, label: '' },
    { value: 4, label: '' }
  ];
  private reminderUnitOptions = [
    { value: 1, label: '' },
    { value: 2, label: '' },
    { value: 3, label: '' },
    { value: 4, label: '' },
    { value: 5, label: '' },
    { value: 6, label: '' },
    { value: 7, label: '' }
  ];
  id: any = null;
  @Watch('id')
  idChange(val) {
    if (val) {
      reminderViewEvent({ id: val }).then((res) => {
        if (!res.error) {
          let rule: Array<any> = [];
          if (typeof res.rule === 'string') {
            rule = JSON.parse(res.rule);
          }
          this.ruleForm = { ...res.data, rule };
        } else {
          this.$message.error(`:${res.error.message}`);
        }
      });
    }
  }
  relaId: any = null;

  submitForm(formName: any) {
    const _ref: any = this.$refs;
    _ref[formName].validate((valid: any) => {
      if (valid) {
        // id
        const rule = JSON.stringify(this.treeRef.getCurrentRule());
        const data = {
          ...this.ruleForm,
          rule,
          id: this.id,
          relaId: this.relaId,
          taskId: this.$route.query.id || this.$route.query.copyId,
          eventType: 3,
          type: 1,
          executionType: this.trans[this.registtabActive]
        };
        if (!this.id) {
          // 
          reminderSaveEvent(data).then((res) => {
            if (!res.error) {
              data.id = res.data;
              this.updateEventRegistpool({
                type: this.registtabActive,
                data,
                addIndex: -1
              });
              this.cancelDialog();
            } else {
              this.$message.error(`:${res.error.message}`);
            }
          });
        } else {
          reminderUpdateEvent(data).then((res) => {
            if (!res.error) {
              data.id = res.data;
              this.updateEventRegistpool({
                type: this.registtabActive,
                data,
                addIndex: this.addIndex
              });
              this.cancelDialog();
            } else {
              this.$message.error(`:${res.error.message}`);
            }
          });
        }
      } else {
        return false;
      }
    });
  }
  reset() {
    this.ruleForm = {
      id: null,
      registerName: '',
      reminderType: 1, // 
      noticeTile: '', // 
      noticeContent: '', // 
      requenceNum: 0, // 
      startTime: '',
      endTime: '',
      rule: '',
      cc: '',
      type: 1,
      weChatUser: '',
      recipient: '',
      phoneNumber: ''
    };
    this.ruleFormRef.clearValidate();
  }

  cancelDialog() {
    this.id = '';
    this.reset();
    this.dialogVisible = false;
  }
}
