




























































































































































import NumberInput from '@components/NumberInput/index.vue';
import decisionRuleEditor from './decisionRuleEditor.vue';

import { Component, Vue, Prop, Ref } from 'vue-property-decorator';
import cloneDeep from 'lodash/cloneDeep';
import { namespace } from 'vuex-class';
const taskLibraryManage = namespace('taskLibraryManage');
@Component({
  components: { decisionRuleEditor, NumberInput }
})
export default class Timing extends Vue {
  @Prop({ default: 'manual' }) launching: string;
  @Ref() ruleForm;
  @taskLibraryManage.State('taskTimingList') taskTimingList;
  @taskLibraryManage.Mutation('UPDATE_LAUNCHING') update_launching;
  @taskLibraryManage.Mutation('DEL_LAUNCHING') del_launching;

  private editIndex = -1;
  private showEditPanel = false;

  private timingForm: Record<string, any> = {
    timingName: '',
    offsetDay: '', // 
    offsetUnit: '', // 
    startUnit: '', // 
    startFreq: '', //  
    execTime: '', // 
    startDate: '', // 
    endDate: '', // 
    allowSplit: 0, // 
    isHand: 0, // 
    dataViewId: '',
    isInstances: '',
    isInstancesCondition: '',
    ruleArray: []
  };
  get timingFormList() {
    return this.taskTimingList;
  }

  private isInstancesOptions: Array<any> = [
    {
      value: 0,
      label: ''
    },
    {
      value: 1,
      label: ''
    }
  ];
  private isInstancesConditionOptions: Array<any> = [
    {
      value: 0,
      label: ''
    },
    {
      value: 1,
      label: ''
    }
  ];
  private rules: any = {
    timingName: [{ required: true, message: '', trigger: 'blur' }]
  };
  private offsetUnitList: Array<any> = [
    { value: 8, label: '' },
    { value: 7, label: '' },
    { value: 6, label: '' },
    { value: 5, label: '' },
    { value: 4, label: '' },
    { value: 3, label: '' },
    { value: 2, label: '' },
    { value: 1, label: '' },
    { value: 0, label: '' }
  ];
  private deviationTypeList: Array<any> = [
    { value: 0, label: '' },
    { value: 1, label: '' }
  ];

  addItem() {
    this.resetForm();
    this.showEditPanel = true;
  }
  editItem(item, index) {
    this.showEditPanel = true;
    this.timingForm = cloneDeep(item);
    this.editIndex = index;
  }
  delItem(item, index) {
    this.del_launching({
      type: 'taskTimingList',
      index
    });
    this.resetForm();
  }
  saveData() {
    this.ruleForm.validate((valid) => {
      if (valid) {
        if (this.timingForm.startDate > this.timingForm.endDate) {
          this.$message.error('');
          return false;
        }
        console.log(this.timingForm);
        const { editIndex, timingForm } = this;
        this.update_launching({
          type: 'taskTimingList',
          data: cloneDeep(timingForm),
          editIndex
        });
        this.$nextTick(() => {
          this.editIndex = -1;
          this.resetForm();
        });
      } else {
        return false;
      }
    });
  }
  resetForm() {
    this.timingForm = {
      timingName: '',
      offsetDay: '', // num
      offsetUnit: '', // 
      startUnit: '', // 
      startFreq: '', // 
      execTime: '', // 
      startDate: '', // 
      endDate: '', // 
      allowSplit: 0, // 
      isHand: 0, // 
      dataViewId: '',
      isInstances: '',
      isInstancesCondition: '',
      ruleArray: []
    };
  }
}
